.hotel {
  background-color: #fff;
  padding: 8rem 0 4rem; }
  .hotel * {
    color: #2e3842; }
  .hotel h2 {
    font-weight: 300;
    letter-spacing: 0.1125rem; }
  .hotel__description {
    margin-bottom: 1rem;
    margin-right: 8vw; }
  .hotel__tab-list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 1rem; }
  .hotel__tab {
    border-bottom: solid 3px #fff;
    cursor: pointer;
    display: block;
    font-size: 0.8rem;
    font-weight: 600;
    letter-spacing: 0.18rem;
    line-height: 2.5rem;
    margin-right: 2.2rem;
    transition: color 250ms, border-bottom-color 250ms; }
    .hotel__tab:hover, .hotel__tab:focus, .hotel__tab:active {
      border-bottom: solid 3px #ccaf9f;
      color: #ccaf9f; }
    .hotel__tab--active {
      border-bottom: solid 3px #ccaf9f;
      color: #ccaf9f; }
  .hotel__tab-panel {
    display: none;
    opacity: 1;
    position: relative;
    transition: opacity 1s; }
    .hotel__tab-panel--active {
      display: block; }
    .hotel__tab-panel--animating {
      opacity: 0;
      transition: opacity 0ms; }
  .hotel__tab-image {
    width: 100%; }
  .hotel__tab-overlay {
    background: linear-gradient(rgba(127, 110, 101, 0.2) 0%, #7F6E65 84%);
    display: flex;
    flex-direction: column;
    padding: 0 1rem 4rem 4rem;
    height: 100%;
    justify-content: flex-end;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 2; }
    .hotel__tab-overlay h3 {
      color: #fff; }
    .hotel__tab-overlay p {
      color: #fff;
      margin-bottom: 0; }
  .hotel__tabless-container {
    margin-bottom: 1rem;
    position: relative;
    width: 100%; }
    .hotel__tabless-container:last-child {
      margin-bottom: 0; }
    .hotel__tabless-container:before {
      content: "";
      display: block;
      padding-top: 144%; }
  .hotel__tabless-image {
    background-position: center;
    background-repeat: no-repeat;
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0; }

@media screen and (max-width: 980px) {
  .hotel {
    padding: 4rem 0 2rem; } }

@media screen and (max-width: 736px) {
  .hotel {
    padding: 3rem 0 2rem; }
    .hotel__tab-list {
      flex-direction: column; }
    .hotel__tab-overlay {
      padding: 1.5rem; }
      .hotel__tab-overlay h3 {
        line-height: 1.25;
        margin-bottom: 0.5rem; }
      .hotel__tab-overlay p {
        line-height: 1.25; } }



