.activities {
  background-color: #fff;
  padding: 4rem 0 5.625rem; }
  .activities * {
    color: #2e3842; }

.activity-card {
  padding-right: 1rem; }
  .activity-card__container {
    position: relative; }
  .activity-card__img {
    width: 100%; }
  .activity-card__overlay {
    background: linear-gradient(rgba(33, 223, 209, 0) 52%, #1A9986 100%);
    display: flex;
    flex-direction: column;
    padding: 2rem 1.5rem;
    position: absolute;
    height: 100%;
    justify-content: flex-end;
    left: 0;
    top: 0;
    width: 100%; }
    .activity-card__overlay h3 {
      color: #fff; }
    .activity-card__overlay p {
      color: #fff;
      margin-bottom: 0; }

@media screen and (max-width: 980px) {
  .activities {
    padding: 2rem 0 3.625rem; } }

@media screen and (max-width: 736px) {
  .activities {
    padding: 2rem 0 1rem; }
  .activity-card {
    padding: 0;
    margin: 0 0 1rem 0; }
    .activity-card__overlay {
      padding: 1rem; } }



