.hero-img {
  background-image: url("/static/images/banner.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  left: 0;
  opacity: 1;
  position: absolute;
  right: 0;
  top: 0;
  transition: opacity 3s linear 2s;
  width: 100vw;
  z-index: -2; }
  .hero-img--loading {
    opacity: 0; }

.hero-overlay {
  background: linear-gradient(transparent 52%, #1a9986 100%);
  height: 100vh;
  left: 0;
  position: absolute;
  top: 0;
  transition: opacity 3s linear 2s;
  width: 100vw;
  z-index: -1; }
  .hero-overlay--loading {
    opacity: 0; }

.hero-text {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100vh;
  padding-bottom: 16vh; }
  .hero-text__h2 {
    font-size: 1.75rem;
    font-weight: 800;
    letter-spacing: 0.39375rem;
    line-height: 3.0625rem;
    margin-bottom: 1.75rem;
    transition: opacity 3s; }
    .hero-text__h2--loading {
      opacity: 0; }
  .hero-text__p {
    margin: 0;
    transition: opacity 3s linear 2s; }
    .hero-text__p--loading {
      opacity: 0; }

@media screen and (max-width: 736px) {
  .hero-text__h2 {
    font-size: 1.25rem; } }



